import React, { useState } from 'react'

import { ConfirmDialog } from 'mui-confirm-dialog'

const App = () => {
  const [open, setOpen] = useState(true)

  const toggleDialog = () => {
    setOpen((st) => !st)
  }

  const onSuccess = () => {
    console.log('success')
    toggleDialog()
    // Do Anything on Success
  }

  return (
    <ConfirmDialog
      open={open}
      toggleDialog={toggleDialog}
      dialogTitle='Perform This Action ?'
      success={onSuccess}
    />
  )
}

export default App
